import axios from 'axios';

import React, { Component } from 'react';

import endpoints from '../../redux/api/endpoints';
import { parseErrorData } from '../../redux/api/utils';


export function SalesTable (props) {
  const { sales } = props

  return (
    <table className='table table-sm table-responsive'>
      <thead>
        <th></th>
        <th></th>
        <th> </th>
        <th></th>
        <th>()</th>
        <th></th>
      </thead>
      <tbody>
        {sales.map(sale => 
          <tr>
            <td>{sale.date}</td>
            <td>
              {sale.lumber_records.map(lumber =>
                <span className='d-block mb-2' style={{lineHeight: '16px'}}>
                  <span className='mr-1 d-block'>{lumber.lumber}</span>
                  <span className='mr-1'>{lumber.quantity}</span>
                  <span className='mr-1'>{lumber.volume}3</span>
                  <span className=''>{lumber.rate}</span>
                </span>
                )}
            </td>
            <td>{sale.volume}3</td>
            <td>
              {sale.cash}
            </td>
            <td>
              {sale.add_expenses}
            </td>
            <td>
              {sale.note}
            </td>
          </tr>
        )}
      </tbody>
    </table>
  )
}


export class SaleList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      saleList: [],
      
      message: null,
      error: null,
    }
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    axios({
      method: 'get',
      url: endpoints.MANAGER_SALE_LIST,
      headers: {'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ ...this.state, saleList: response.data.results });
    })
  }

  render() {
    const { saleList } = this.state
    return (
      <div className='mt-2'>
        <div className='card card-style mb-2'>
          <div className='content'>
            <h4 className='mb-2'> ({saleList.length})</h4>
            {saleList.length > 0 
              ? <SalesTable sales={saleList} />
              : <h5> </h5>
            }
          </div>
        </div>
      </div>
    )
  }
}