import axios from 'axios';

import React, { Component, useState  } from 'react';

import endpoints from '../../redux/api/endpoints';
import { replaceItemInDictArrayById, lodashToggle, getObjectbyId } 
  from '../../components/utils';
import { parseErrorData } from '../../redux/api/utils';

import { CreatedSale, AddNote, LumberToSale, AddExpensesComp, SaleToCreate } 
  from '../../components/kladman/SaleCreate';


export class SaleCreateComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pineBrus: [],
      larchBrus: [],
      pineDoska: [],
      larchDoska: [],
      lumbers: [],

      lumbersToSale: [],
      totalVolume: 0.0,
      totaCash: 0,
      note: '',
      addExpenses: 0,

      dataToSave: null,
      
      createdSale: null,
      message: null,
      error: null,
    }

    this.setData = this.setData.bind(this);
    this.addLumberToSale = this.addLumberToSale.bind(this);
    this.setLumberID = this.setLumberID.bind(this);
    this.calcRowQnty = this.calcRowQnty.bind(this);
    this.calcRowCash = this.calcRowCash.bind(this);
    this.calcTotal = this.calcTotal.bind(this);
    this.setRowCash = this.setRowCash.bind(this);
    this.saveData = this.saveData.bind(this);
    this.setNote = this.setNote.bind(this);
    this.setAddExpenses = this.setAddExpenses.bind(this);
    this.preSave = this.preSave.bind(this);
    this.back = this.back.bind(this);
  }

  componentDidMount() {
    axios.get(endpoints.KLADMAN_SALE_INIT_DATA)
      .then(res => {
        const initData = res.data;
        this.setState({ 
          pineBrus: initData.pine_brus_lumbers,
          larchBrus: initData.larch_brus_lumbers,
          pineDoska: initData.pine_doska_lumbers,
          larchDoska: initData.larch_doska_lumbers,
          lumbers: initData.lumbers,
         });
      })
  }

  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  addLumberToSale () {
    this.setState({
      ...this.state,
      lumbersToSale: [
        ...this.state.lumbersToSale,
        {id: this.state.lumbersToSale.length}
      ]
    })
  }

  setLumberID (e, id) {
    let { lumbersToSale, lumbers } = this.state
    let lumberData = getObjectbyId(lumbers, e.target.value)
    lumberData = { ...lumberData, id: id }
    
    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumberData)
    this.setState({
      lumbersToSale: lumbersToSale
    })
  }

  calcRowQnty (e, id) {
    let { lumbersToSale, addExpenses } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.quantity =  e.target.value ? parseInt(e.target.value) : 0
    lumber.volume_total = (lumber.volume * lumber.quantity).toFixed(4)
    lumber.cash = (lumber.volume_total * lumber.rate).toFixed(0)

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale, addExpenses)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  calcRowCash (e, id) {
    let { lumbersToSale, addExpenses } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.rate =  e.target.value ? parseInt(e.target.value) : 0
    lumber.cash = (lumber.volume_total * lumber.rate).toFixed(0)

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale, addExpenses)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  setRowCash (e, id) {
    let { lumbersToSale, addExpenses } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.cash =  e.target.value ? parseInt(e.target.value) : 0

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale, addExpenses)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  calcTotal (lumbers, addExpenses) {
    let totalCash = 0
    let totalVolume = 0.0
    lumbers.map(lumber => {
      totalCash += parseInt(lumber.cash)
      totalVolume += parseFloat(lumber.volume_total)
    })
    totalCash += parseFloat(addExpenses)

    return { totalCash: totalCash.toFixed(0), totalVolume: totalVolume.toFixed(4) }
  }

  setNote (e) {
    this.setState({
      note: e.target.value
    })
  }

  setAddExpenses (e) {
    let { lumbersToSale } = this.state
    const addExpenses = e.target.value ? parseInt(e.target.value) : 0
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale, addExpenses)
    this.setState({
      addExpenses: addExpenses,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  preSave () {
    const { lumbersToSale, totalCash, totalVolume, addExpenses, note } = this.state
    let raw_records = []
    lumbersToSale.map(lumber =>{
      if (lumber.cash > 0 && lumber.quantity > 0) {
        raw_records = lodashToggle(raw_records, lumber)
      }
    })
    let data = {
      raw_records: raw_records,
      date: null,
      cash: parseFloat(totalCash),
      volume: parseFloat(totalVolume),
      note: note,
      add_expenses: addExpenses,
    }
    this.setState({
      dataToSave: data,
    })
  }

  saveData () {
    const { dataToSave } = this.state
    const token = localStorage.getItem('token');
    
    axios({
      method: 'post',
      url: endpoints.KLADMAN_SALE_CREATE,
      data: dataToSave,
      headers: { 'content-type': 'application/JSON', 'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ message: response.data.message, createdSale: response.data.sale });
    })
    .catch(err => {
        // const error = new Error(err);
        // error.data = parseErrorData(err);
        this.setState({ message: '' });
        // throw error;
    })

    this.setState({
      dataToSave: null,
      lumbersToSale: [],
      totalVolume: 0.0,
      totaCash: 0,
      note: '',
      addExpenses: 0,
    })
  }

  back () {
    this.setState({dataToSave: null})
  }

  render() {
    const { lumbersToSale, createdSale, totalCash, totalVolume, note, addExpenses, dataToSave, message } 
      = this.state
    return (
      <div>
        {createdSale 
          ? <CreatedSale sale={createdSale} message={message}/>
          : dataToSave
            ? <SaleToCreate sale={dataToSave} saveData={this.saveData} back={this.back}/>
            : <div className='card card-style mb-1 mt-2 mx-1'>
                <div className='content'>
                  <div className='d-flex justify-content-between mb-2 text-center'>
                    <h2> ?</h2>
                    <button className='btn btn-sm bg-teal-light' onClick={this.clear}> </button>
                  </div>
                  
                  {lumbersToSale.length > 0  && lumbersToSale.map(lumber => lumber &&
                    <LumberToSale 
                      lumber={lumber} 
                      setLumberID={this.setLumberID} 
                      calcRowQnty={this.calcRowQnty}
                      calcRowCash={this.calcRowCash}
                      setRowCash={this.setRowCash}
                      pineBrus={this.state.pineBrus}
                      larchBrus={this.state.larchBrus}
                      pineDoska={this.state.pineDoska}
                      larchDoska={this.state.larchDoska}
                    />
                  )}
                  <button className='mt-1 btn btn-l bg-highlight' onClick={this.addLumberToSale}>
                     
                  </button>

                  <AddExpensesComp addExpenses={addExpenses} setAddExpenses={this.setAddExpenses}/>
                  
                  {totalCash > 0 &&
                    <div className='d-flex justify-content-between my-2'>
                      <p className='font-20'> : {totalVolume} 3</p>
                      <p className='font-20'> : {totalCash} </p>
                    </div>
                  }
                  <AddNote note={note} setNote={this.setData}/>
                  {totalCash > 0 &&
                    <button
                      onClick={this.preSave}
                      className='btn btn-center-xl btn-xxl text-uppercase font-900 bg-highlight rounded-sm 
                        shadow-l mt-2'>
                      
                    </button>
                  }
                </div>
            </div>
          }
      </div>
    )
  }
}