import axios from 'axios';

import React, { Component, useState  } from 'react';

import endpoints from '../../redux/api/endpoints';
import { replaceItemInDictArrayById, lodashToggle, getObjectbyId } 
  from '../../components/utils';
import { parseErrorData } from '../../redux/api/utils';

import { CreatedSale, AddNote, LumberToSale, DeliveryFeeComp, SaleSchema1ToCreate, Schema1CheckList } 
  from '../../components/kladman/SaleCreateSchema1';


export class SaleCreateSchema1Container extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pineBrus: [],
      larchBrus: [],
      pineDoska: [],
      larchDoska: [],
      lumbers: [],

      lumbersToSale: [],
      totalVolume: 0.0,
      totaCash: 0,
      
      loader: true,
      seller: null,
      bonusKladman: true,      
      
      note: '',
      addExpenses: 0,
      deliveryFee: 0,

      dataToSave: null,
      
      createdSale: null,
      message: null,
      error: null,
    }

    this.setData = this.setData.bind(this);
    this.addLumberToSale = this.addLumberToSale.bind(this);
    this.setLumberID = this.setLumberID.bind(this);
    this.calcRowQnty = this.calcRowQnty.bind(this);
    this.calcRowCash = this.calcRowCash.bind(this);
    this.calcRowRamaCash = this.calcRowRamaCash.bind(this);
    this.calcTotal = this.calcTotal.bind(this);
    this.setRowCash = this.setRowCash.bind(this);
    this.saveData = this.saveData.bind(this);
    this.setNote = this.setNote.bind(this);
    this.setDeliveryFee = this.setDeliveryFee.bind(this);
    this.preSave = this.preSave.bind(this);
    this.back = this.back.bind(this);
    this.checkLoader = this.checkLoader.bind(this);
  }

  componentDidMount() {
    const token = localStorage.getItem('token');
    
    axios({
      method: 'get',
      url: endpoints.KLADMAN_SALE_INIT_DATA,
      headers: { 'Authorization': `JWT ${token}` }
    })
    .then(res => {
      const initData = res.data;
      this.setState({ 
        pineBrus: initData.pine_brus_lumbers,
        larchBrus: initData.larch_brus_lumbers,
        pineDoska: initData.pine_doska_lumbers,
        larchDoska: initData.larch_doska_lumbers,
        lumbers: initData.lumbers,
        sellers: initData.sellers,
        kladmanId: initData.kladman_id
        });
    })
  }

  setData (e) {
    this.setState({
      ...this.state,
      [e.target.name]: e.target.value
    })
  }

  addLumberToSale () {
    this.setState({
      ...this.state,
      lumbersToSale: [
        ...this.state.lumbersToSale,
        {id: this.state.lumbersToSale.length}
      ]
    })
  }

  setLumberID (e, id) {
    let { lumbersToSale, lumbers } = this.state
    let lumberData = getObjectbyId(lumbers, e.target.value)
    lumberData = { ...lumberData, id: id }
    
    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumberData)
    this.setState({
      lumbersToSale: lumbersToSale
    })
  }

  calcRowQnty (e, id) {
    let { lumbersToSale } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.quantity =  e.target.value ? parseInt(e.target.value) : 0
    lumber.volume_total = (lumber.volume * lumber.quantity).toFixed(4)
    lumber.selling_total_cash = (lumber.volume_total * lumber.selling_price).toFixed(0)

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  calcRowCash (e, id) {
    let { lumbersToSale } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.selling_price =  e.target.value ? parseInt(e.target.value) : 0
    lumber.selling_total_cash = (lumber.volume_total * lumber.selling_price).toFixed(0)

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  calcRowRamaCash (e, id) {
    let { lumbersToSale } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.rama_price =  e.target.value ? parseInt(e.target.value) : 0
    lumber.rama_total_cash = (lumber.volume_total * lumber.rama_price).toFixed(0)

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  setRowCash (e, id) {
    let { lumbersToSale } = this.state
    let lumber = getObjectbyId(lumbersToSale, id)
    lumber.selling_total_cash =  e.target.value ? parseInt(e.target.value) : 0

    lumbersToSale = replaceItemInDictArrayById(lumbersToSale, lumber)
    const { totalCash, totalVolume } = this.calcTotal(lumbersToSale)

    this.setState({
      lumbersToSale: lumbersToSale,
      totalCash: totalCash,
      totalVolume: totalVolume
    })
  }

  calcTotal (lumbers) {
    let totalCash = 0
    let totalVolume = 0.0
    lumbers.map(lumber => {
      totalCash += parseInt(lumber.selling_total_cash)
      totalVolume += parseFloat(lumber.volume_total)
    })
    return { totalCash: totalCash.toFixed(0), totalVolume: totalVolume.toFixed(4) }
  }

  setNote (e) {
    this.setState({
      note: e.target.value
    })
  }

  setDeliveryFee (e) {
    const deliveryFee = e.target.value ? parseInt(e.target.value) : 0
    this.setState({
      deliveryFee: deliveryFee,
    })
  }

  preSave () {
    const { lumbersToSale, loader, seller, bonusKladman, note, addExpenses, deliveryFee, totalCash, 
      totalVolume, kladmanId } = this.state
    let raw_records = []
    lumbersToSale.map(lumber =>{
      if (lumber.selling_total_cash > 0 && lumber.quantity > 0) {
        raw_records = lodashToggle(raw_records, lumber)
      }
    })

    let data = {
      raw_records: raw_records,
      date: null,
      loader: loader,
      seller: seller,
      bonus_kladman: bonusKladman ? kladmanId : null,
      note: note,
      add_expenses: addExpenses,
      delivery_fee: deliveryFee,

      sale_cash: parseFloat(totalCash),
      volume: parseFloat(totalVolume),
    }
    this.setState({
      dataToSave: data,
    })
  }

  saveData () {
    const { dataToSave } = this.state
    const token = localStorage.getItem('token');
    
    axios({
      method: 'post',
      url: endpoints.KLADMAN_SALE_SCHEMA1_CREATE,
      data: dataToSave,
      headers: { 'content-type': 'application/JSON', 'Authorization': `JWT ${token}` }
    })
    .then(response => {
      this.setState({ message: response.data.message, createdSale: response.data.sale });
    })
    .catch(err => {
        // const error = new Error(err);
        // error.data = parseErrorData(err);
        this.setState({ message: '' });
        // throw error;
    })

    this.setState({
      dataToSave: null,
      lumbersToSale: [],
      totalVolume: 0.0,
      totaCash: 0,
      note: '',
      addExpenses: 0,
    })
  }

  back () {
    this.setState({dataToSave: null})
  }

  checkLoader(e) {
    if (e.target.name === 'seller')
      this.setState({[e.target.name]: e.target.value})
    else
      this.setState({[e.target.name]: !this.state[e.target.name]})
  }

  render() {
    const { lumbersToSale, createdSale, totalCash, totalVolume, note, addExpenses, dataToSave, message } 
      = this.state
    return (
      <div>
        {createdSale 
          ? <CreatedSale sale={createdSale} message={message}/>
          : dataToSave
            ? <SaleSchema1ToCreate sale={dataToSave} saveData={this.saveData} back={this.back} 
              sellers={this.state.sellers}/>
            : <div className='card card-style mb-1 mt-2 mx-1'>
                <div className='content'>
                  <div className='d-flex justify-content-between mb-2 text-center'>
                    <h2> ?</h2>
                    <button className='btn btn-sm bg-teal-light' onClick={this.clear}> </button>
                  </div>
                  
                  {lumbersToSale.length > 0  && lumbersToSale.map(lumber => lumber &&
                    <LumberToSale 
                      lumber={lumber} 
                      setLumberID={this.setLumberID} 
                      calcRowQnty={this.calcRowQnty}
                      calcRowCash={this.calcRowCash}
                      calcRowRamaCash={this.calcRowRamaCash}
                      setRowCash={this.setRowCash}
                      pineBrus={this.state.pineBrus}
                      larchBrus={this.state.larchBrus}
                      pineDoska={this.state.pineDoska}
                      larchDoska={this.state.larchDoska}
                    />
                  )}
                  <button className='mt-1 mb-2 btn btn-l bg-highlight' onClick={this.addLumberToSale}>
                     
                  </button>
                  <div className='divider my-2'></div>
                  
                  <Schema1CheckList loader={this.state.loader} checkLoader={this.checkLoader}
                    seller={this.state.seller} bonusKladman={this.state.bonusKladman}
                    sellers={this.state.sellers}
                    />
                  <DeliveryFeeComp deliveryFee={this.state.deliveryFee} setDeliveryFee={this.setDeliveryFee}/>
                  
                  {totalCash > 0 &&
                    <div className='d-flex justify-content-between my-2'>
                      <p className='font-20'> : {totalVolume} 3</p>
                      <p className='font-20'> : {totalCash} </p>
                    </div>
                  }
                  <AddNote note={note} setNote={this.setData}/>
                  {totalCash > 0 &&
                    <button
                      onClick={this.preSave}
                      className='btn btn-center-xl btn-xxl text-uppercase font-900 bg-highlight rounded-sm 
                        shadow-l mt-2'>
                      
                    </button>
                  }
                </div>
            </div>
          }
      </div>
    )
  }
}